body {
  margin: 0;
}

.stars {
  display: flex;
  align-items: center;
}

.star {
  width: 15px;
  height: 15px;
  margin-right: 4px;
  background-image: url(./images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.stars--1 > :first-child,
.stars--2 > :nth-child(-n+2),
.stars--3 > :nth-child(-n+3),
.stars--4 > :nth-child(-n+4),
.stars--5 > * {
  background-image: url(./images/star-active.svg);
}
